.icons{
    width: 205px;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-end;

    padding: 0 1.5rem;
    box-sizing: border-box;

    font-size: 1.5rem;
}

.icons > *{
    margin: 0 .75rem;
}

.icons > *:hover{
    cursor: pointer;
    opacity: .8;
}